<template>
  <a-layout-header class="header" style="display: flex">
    <div class="logo"/>
    <div class="menu">
      <a-menu
          theme="dark"
          mode="horizontal"
          v-model:selectedKeys="selectedKeys"
          :style="{ lineHeight: '64px' }"
      >
        <a-menu-item key="/welcome">
          <router-link to="/welcome">
            欢迎页
          </router-link>
        </a-menu-item>
        <a-menu-item key="/passenger">
          <router-link to="/passenger">
            <span style="margin-left: 10px">乘客人管理</span>
          </router-link>
        </a-menu-item>
        <a-menu-item key="/ticket">
          <router-link to="/ticket">
            <span style="margin-left: 10px">余票查询</span>
          </router-link>
        </a-menu-item>
        <a-menu-item key="/my-ticket">
          <router-link to="/my-ticket">
            <user-outlined/>
            <span style="margin-left: 10px">我的车票</span>
          </router-link>
        </a-menu-item>
      </a-menu>

    </div>

    <a-switch v-model:checked="flag" @click="changeTheme">
      <template #checkedChildren>
        <icon :style="{ fontSize:'1px'}">
          <template #component>
            <svg   t="1721285216005" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1470" width="20em" height="20em">
              <path d="M617.813333 512c0-174.08 85.333333-334.506667 225.28-433.493333 6.826667-3.413333 6.826667-10.24 6.826667-17.066667s-3.413333-10.24-10.24-13.653333c-54.613333-20.48-112.64-30.72-170.666667-30.72C395.946667 17.066667 174.08 238.933333 174.08 512s221.866667 494.933333 494.933333 494.933333c58.026667 0 116.053333-10.24 170.666667-30.72 6.826667-3.413333 10.24-6.826667 10.24-13.653333s-3.413333-13.653333-6.826667-17.066667c-139.946667-98.986667-225.28-259.413333-225.28-433.493333z m-85.333333 256c27.306667 0 51.2 23.893333 51.2 51.2s-23.893333 51.2-51.2 51.2-51.2-23.893333-51.2-51.2 20.48-51.2 51.2-51.2z m-17.066667-204.8c0 58.026667-44.373333 102.4-102.4 102.4s-102.4-44.373333-102.4-102.4 44.373333-102.4 102.4-102.4 102.4 44.373333 102.4 102.4z" fill="#2c2c2c" p-id="1471"></path>
            </svg>
          </template>
        </icon>
      </template>
      <template #unCheckedChildren>

      </template>
    </a-switch>
    <div class="admin" style="color: white;font-weight: 400;font-size: 17px;display: flex">
      <div style="flex: 2">
        <span style="white-space: nowrap;text-overflow: ellipsis">你好! {{ member.mobile }}</span>
      </div>
      <div class="logout" style="flex: 2">
        <router-link to="/login" @click="logout">退出系统</router-link>
      </div>
    </div>
  </a-layout-header>
</template>

<script setup>
import store from "@/store";
import {message} from "ant-design-vue";
import {ref, watch} from "vue";
import Icon from '@ant-design/icons-vue';
import router from "@/router";
import {UserOutlined} from "@ant-design/icons-vue";

const flag = ref(false)
const selectedKeys = ref([])
const logout = () => {
  store.commit("setMember", {})
  message.success("退出成功")
}

function changeTheme() {
  // flag.value = !flag.value
  window.document.documentElement.setAttribute('data-doc-theme', flag.value ? 'dark' : '');
}

watch(() => router.currentRoute.value.path, (newValue) => {
  selectedKeys.value = []
  selectedKeys.value.push(newValue)
}, {immediate: true})

const member = JSON.parse(localStorage.getItem("member"))
</script>

